import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { FuseConfigService } from '@fuse/services/config.service';
import { fuseAnimations } from '@fuse/animations';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
    selector   : 'coming-soon',
    templateUrl: './coming-soon.component.html',
    styleUrls  : ['./coming-soon.component.scss'],
    animations : fuseAnimations
})
export class FuseComingSoonComponent implements OnInit
{
    comingSoonForm: FormGroup;
    comingSoonFormErrors: any;
    subscribed:boolean = false;
    constructor(
        private fuseConfig: FuseConfigService,
        private formBuilder: FormBuilder,
        private http: HttpClient
    )
    {
        this.fuseConfig.setConfig({
            layout: {
                navigation: 'none',
                toolbar   : 'none',
                footer    : 'none'
            }
        });

        this.comingSoonFormErrors = {
            email: {}
        };
    }

    ngOnInit()
    {
        this.subscribed = localStorage.getItem('subscribed') == 'true';
        this.comingSoonForm = this.formBuilder.group({
            email: ['', [Validators.required, Validators.email]]
        });

        this.comingSoonForm.valueChanges.subscribe(() => {
            this.onRegisterFormValuesChanged();
        });
    }

    onRegisterFormValuesChanged()
    {
        for ( const field in this.comingSoonFormErrors )
        {
            if ( !this.comingSoonFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.comingSoonFormErrors[field] = {};

            // Get the control
            const control = this.comingSoonForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.comingSoonFormErrors[field] = control.errors;
            }
        }
    }
    subscribe(){
        let email = this.comingSoonForm.get('email').value;
        let headers = new HttpHeaders({
            'Authorization':'Basic RW1haWxQb3N0VXNlcjpFbWFpbFBvc3RVc2Vy',
            'Content-Type':'application/json'
        });
        console.log(headers);
        this.http.get<any[]>(`https://baas.kinvey.com/appdata/kid_H1QReKgV7/Emails?query={"email":"${email}"}`,
        {headers: headers})
        .subscribe(result=>{
            let response = result;
            if(result.length != 0){
                
                this.subscribed = true;
                localStorage.setItem('subscribed','true');
            } else{
                
                this.http.post('https://baas.kinvey.com/appdata/kid_H1QReKgV7/Emails',{
                    email: email
                },{headers: headers}).subscribe(result=>{
                    this.subscribed = true;
                    localStorage.setItem('subscribed','true');
                });
            }
        });
    }
}
